.message-window {
  font-family: var(--normal-font);
  letter-spacing: 0px;
  align-self: center;
  width: clamp(300px, 26%, 450px);
  height: 70vh;
  box-sizing: border-box;
  background-color: #00000066;
  padding: 24px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow-y: auto;
  gap:6px;
}


.message-window.slidein {
  animation: 1000ms cubic-bezier(0.12, 1.07, 1, 1) 0s 1 normal forwards running messagewindowslidein;
}

.message-window.slideout {
  animation: 400ms ease-in 0s 1 reverse forwards running messagewindowslidein;
}

@keyframes messagewindowslidein {
  from {  
    transform: translate(100px);
    opacity: 0;
  }
  to {
    transform: translate(0);
    opacity: 1;
  }
}


.message-window p {
  line-height: 1.2rem;
}